}

/* 
! Header Responsive 
*/

@media (min-width: 2000px) {
  .section_header_v2 .header_v2_background-wrapper {
    margin-right: 25%;
  }
}

/* 
! Testimonial Slider - HP
*/

.hp_testimonials_paragraph {
  display: -webkit-box; /* Utilisé pour créer un conteneur flexible */
  -webkit-box-orient: vertical; /* Définit l'orientation du conteneur */
  -webkit-line-clamp: 9; /* Limite le nombre de lignes visibles à 3 */
  line-clamp: 9;
  overflow: hidden; /* Cache le texte qui dépasse */
  text-overflow: ellipsis; /* Ajoute des points de suspension (...) à la fin */
}
/* 
! Picture Slider
*/

.swiper-slide.is-slider-picture {
  opacity: 1;
  transition: opacity 0.6s ease-in-out; /* Durée de la transition augmentée */
}

.swiper-slide.is-slider-picture.fade-out {
  opacity: 0.9; /* État de fondu */
}

/* 
! Partners Coop in Focus Pays
*/
#pays-src {
  opacity: 0;
  position: absolute;
}

.coop_origin_same-country {
  display: none; /* État initial */
}
